// import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
// import FormControlLabel from '@mui/material/FormControlLabel';
// import Checkbox from '@mui/material/Checkbox';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import React, { useState,useEffect } from 'react'
import {useNavigate} from 'react-router-dom';
// import { useToast } from "@chakra-ui/toast";
// import axios from "axios";
import {ErrorStack} from "./ErrorStack";
import Header from "./Header";

function Copyright(props) {
  return (
    <Typography variant="body2" color="text.secondary" align="center" {...props}>
      {'Copyright  '}
      <Link color="inherit" href="https://mui.com/">
        Keeper
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

// TODO remove, this demo shouldn't need to reset the theme.

const defaultTheme = createTheme();

export const SignUp = () => {
  // const toast = useToast();
    const [username, setName] = useState('')
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [mess,setmess] = useState(false)
    const [errmes,seterrmes] = useState("");

    let navigate = useNavigate();

    useEffect(() => {

    const token = localStorage.getItem('userInfo');
    
    if (token){
      navigate('/home');
    }

  }, [navigate]);

  const handleSubmit = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    console.log({
      email: data.get('email'),
      password: data.get('password'),
    });
  };

  const handleClick = async (e)=>{
    e.preventDefault();

    try {
      // const config = {
      //   headers: {
      //     "Content-type": "application/json",
      //   },
      // };

      

      const response = await fetch(`http://localhost:5000/api/users/signup`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ username, email, password }),
      });


      if (response.ok) {
        const json = await response.json();
        console.log(json);
        if (json.message) {
          setmess(true);
          seterrmes(json.msg);
        } else {
          setmess(false);
          console.log("success");
          localStorage.setItem("userInfo", JSON.stringify(json.data));
          navigate('/home');
        }
      } else {
        // Handle non-successful response
        const errorData = await response.json();
        // You can extract the specific error message from the response if available
        const errorMessage = errorData.message || "An error occurred";
        setmess(true);
        seterrmes(errorMessage);
      }

      // const { data } = await axios.post(
      //   "http://localhost:5000/api/users/signup",
      //   {
      //     username,
      //     email,
      //     password,
      //   },
      //   config
      // );
      // console.log(data);
      // if (data.message){
      //   setmess(true);
      //   seterrmes(data.message);
      // }
      // else{
      //   setmess(false);
      //   console.log("sucess")
      //   localStorage.setItem("userInfo", JSON.stringify(data.data));
      //   navigate('/home');
      // }
    } catch (error) {
      console.error(error);
      setmess(true);
      seterrmes("An error occurred");
    }
    
}

  return (
    <div>
      <Header />
      {mess && <ErrorStack message={errmes}/>}
      <ThemeProvider theme={defaultTheme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Sign up
          </Typography>
          <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 3 }}>
            <Grid container spacing={2}>
              
              
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="username"
                  label="User Name"
                  name="username"
                  autoComplete="username"
                  onChange={e=>{setName(e.target.value)}}
                  
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="email"
                  label="Email"
                  name="email"
                  autoComplete="email"
                  onChange={e=>{setEmail(e.target.value)}}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  name="password"
                  label="Password"
                  type="password"
                  id="password"
                  autoComplete="new-password"
                  onChange={e=>{setPassword(e.target.value)}}
                />
              </Grid>
              
            </Grid>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
              onClick={handleClick}
            >
              Sign Up
            </Button>
            <Grid container justifyContent="flex-end">
              <Grid item>
                <Link href="/" variant="body2">
                  Already have an account? Sign in
                </Link>
              </Grid>
            </Grid>
          </Box>
        </Box>
        <Copyright sx={{ mt: 5 }} />
      </Container>

      

      </ThemeProvider>

    </div>

  
  );
}